import { SignJWT, jwtVerify } from "jose"
import { cookies } from "next/headers"
import { query } from "@/lib/db"
import bcrypt from "bcryptjs"

const key = new TextEncoder().encode(process.env.JWT_SECRET || "fallback-secret-key")

export async function encrypt(payload: any) {
  return await new SignJWT(payload)
    .setProtectedHeader({ alg: "HS256" })
    .setIssuedAt()
    .setExpirationTime("24h")
    .sign(key)
}

export async function decrypt(input: string): Promise<any> {
  const { payload } = await jwtVerify(input, key, {
    algorithms: ["HS256"],
  })
  return payload
}

export async function verifyToken(token: string) {
  try {
    return await decrypt(token)
  } catch {
    return null
  }
}

export async function getCurrentUser() {
  const cookieStore = await cookies()
  const token = cookieStore.get("auth-token")?.value

  console.log("[auth] getCurrentUser called, token exists:", !!token)

  if (!token) {
    console.log("[auth] No auth token found")
    return null
  }

  try {
    const payload = await decrypt(token)
    console.log("[auth] Token decrypted successfully, userId:", payload.userId)
    const users = await query`SELECT * FROM users WHERE id = ${payload.userId}`
    const user = users[0] || null
    console.log("[auth] User found:", !!user)
    return user
  } catch (error) {
    console.error("[auth] Error getting current user:", error)
    return null
  }
}

export async function hashPassword(password: string) {
  return await bcrypt.hash(password, 12)
}

export async function signUp(username: string, password: string, signupCode?: string) {
  const hashedPassword = await hashPassword(password)

  // Set default values based on signup code
  let nameColor = null
  let hasGoldAnimation = false

  if (signupCode === "asdf") {
    nameColor = "#6366f1" // Default indigo color
  } else if (signupCode === "qwea") {
    hasGoldAnimation = true
  }

  const users = await query`
    INSERT INTO users (username, password_hash, signup_code, name_color, has_gold_animation, theme, hue, notifications_enabled, last_active, ui_mode)
    VALUES (${username}, ${hashedPassword}, ${signupCode || null}, ${nameColor}, ${hasGoldAnimation}, 'light', 'blue', false, NOW(), 'full')
    RETURNING *
  `

  const user = users[0]
  const token = await encrypt({ userId: user.id })

  return { user, token }
}

export async function signIn(username: string, password: string) {
  const users = await query`SELECT * FROM users WHERE username = ${username}`
  const user = users[0]

  if (!user) {
    throw new Error("Invalid username or password")
  }

  const isValid = await bcrypt.compare(password, user.password_hash)
  if (!isValid) {
    throw new Error("Invalid username or password")
  }

  const token = await encrypt({ userId: user.id })
  return { user, token }
}
